












import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({})
export default class Button extends Vue {
    @Prop({ default: "the-button" })
    name: string;

    @Prop()
    type: string;

    @Prop()
    activation: boolean;

    handleClick() {
        this.$emit("bClick");
    }
}



