















import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";

@Component({})
export default class Button extends Vue {
    @Prop({ default: "the-input" })
    name: string;

    @Prop({ default: "请输入内容" })
    placeholder: string;

    @Prop({ default: false })
    disabled: boolean;

    @Prop({ default: "text" })
    type: string;
}



